.header-title {
    font-size: 24px;
    font-weight: 700;
    color: #f2f2f2;
    text-decoration: none;
}

.header {
  overflow: hidden;
  background-color: #455a64;
  text-align: center;
}

.nav-link {
  color: #f2f2f2;
  background-color: #455a64;
  text-align: center;
  padding: 0px 10px;
  text-decoration: none;
  font-size: 15px;
  float: left;
  /* these last two make the logout button in
  the nav bar look like another link */
  border: 3px solid #455a64;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  border-bottom: 3px solid #2196f3;
}
.nav-login {
  color: #2196f3;
  text-align: center;
  padding: 0px 10px;
  text-decoration: none;
  font-size: 15px;
  border: 3px solid #455a64;
  outline: 0;
}

.float-right {
  /* color: #ff0000; */
  float: right;
}
/* .active {
  border-bottom: 3px solid #ff0000;
} */
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: #455a64;
    color: #f2f2f2;
    border-top: #2196f3 solid 3px;
}

.footer-content p {
    margin: 0;
    font-size: 12px;
}

.footer-left {
    float: left;
    padding: 10px;
}

.footer-right {
    float: right;
    padding-right: 10px;
    color: #2196f3
}
.service-item {
    text-align: center;
    background-color: #6a7b83;
    margin: 0;
    color: white;
    overflow: hidden;
    padding-bottom: 30px;
    padding-top: 30px;
    border-bottom: #2196f3 solid 3px;
    min-height: 500px;
}
.service-item p {
    width: 90%;
    margin: auto;
}

.service-item Button {
    margin: 15px;
}

.home-card {
    height: 200px;
    margin: 10px;
}
.component-header {
    width: 100%;
    text-align: center;
    background-color: #6a7b83;
    margin: 0;
    padding-top: 30px;
    padding-bottom: 30px;
    overflow: hidden;
    color: #f2f2f2;
    border-bottom: 3px solid #2196f3;
}
.component-header .button-return-left{
    display: block;
    margin: auto;
    position: absolute;
    float: left;
    left: 5%;
    margin-top: 12px; 
}
.component-second {
    padding-top: 15px;
    text-align: center;
    margin: auto;
}
.component-second .add-btn{
    display: block;
    margin: auto;
    width: 150px;
}
.search-field {
    display: block;
    width: 40%;
    min-width: 200px;
    margin-bottom: 0;
    background-color: #f2f2f2;
}

