/* Customer */
.box-container{
    text-align: center;
    width: 520px;
    margin-left: auto;
    margin-right: auto; 
    margin-top: 15px;
    margin-bottom: 15px;
}
.stick-left {
    text-align: left;
}
.box-container .two-box {
    width: 250px;
    display: inline-block;
}
.box-container h4 {
    margin-top: 0;
    margin-bottom: 0;
}

.two-box .stick-right {
    float: right;
    margin-bottom: -17px;
}
/* cards */
.card-container {
    min-height: 100px;
    max-width: 600px;
    /* text-align: center; */
    margin: auto;
    margin-top: 10px;
}
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    height: 100%;
    position: relative;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #00acb0;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

.main-container {
    min-height: 100vh; /* will cover the 100% of viewport */
    display: block;
    padding-bottom: 80px;
   }

